import 'focus-visible';
import React from 'react';
import classNames from 'classnames/bind';
import { RadioButtonGroupItem, RadioButtonGroupProps } from 'core/types/radio';
import { FormContext } from 'shared/Form/formContext';

import RadioButton from './RadioButton';

import styles from './styles.scss';

const cn = classNames.bind(styles);

class RadioButtonGroup extends React.PureComponent<RadioButtonGroupProps> {
  static contextType = FormContext;

  static defaultProps = {
    size: 'default',
    labelAside: false,
    disabled: false,
    uncontrolled: false
  };

  handleOnChange = (event: React.ChangeEvent<HTMLInputElement>): void => {
    const { onChange, fieldName } = this.props;
    onChange(event.target.value, fieldName);
  };

  getSizeClass = (): string => {
    const { size } = this.props;
    const { sizeForm = size } = this.context;

    if (sizeForm === 'small') {
      return styles['radio_group--small'];
    }

    return styles['radio_group--default'];
  };

  renderItem = (item: RadioButtonGroupItem): JSX.Element => {
    const {
      name,
      size,
      activeValue,
      disabled,
      uncontrolled,
      textLimitWithEllipsis,
      labelMaxWidth
    } = this.props;

    const { sizeForm = size } = this.context;

    return (
      <li className={ cn('radio_group_item', `radio_group_item--${ size }`) } key={ item.value }>
        <RadioButton
          name={ name }
          label={ item.label }
          size={ sizeForm }
          value={ item.value }
          checked={ activeValue ? activeValue === item.value : undefined }
          disabled={ disabled || item.disabled }
          uncontrolled={ uncontrolled }
          textLimitWithEllipsis={ textLimitWithEllipsis }
          labelMaxWidth={ labelMaxWidth }
        />
      </li>
    );
  };

  render(): JSX.Element {
    const {
      label,
      items,
      disabled,
      labelAside,
      dataTestId,
      labelWidth
    } = this.props;

    const { labelAsideForm = labelAside, labelWidthForm = labelWidth } = this.context;

    const radioGroupClasses = cn(
      'radio_group',
      this.getSizeClass(),
      {
        'radio_group--label_aside': labelAsideForm,
        'radio_group--disabled': disabled
      }
    );

    return (
      <div
        data-test-id={ dataTestId }
        className={ radioGroupClasses }
        onChange={ this.handleOnChange }
      >
        { label && (
          <div
            style={ { width: labelWidthForm || undefined } }
            data-test-id="radio_group_label"
            className={ styles.radio_group__label }
          >
            { label }
          </div>
        ) }
        <ul className={ styles.radio_group__fieldset } data-test-id={ `${ dataTestId }__item` }>
          { items.map(this.renderItem) }
        </ul>
      </div>
    );
  }
}

export { RadioButtonGroup };
